import {
  Box,
  Button,
  Container,
  Flex,
  Heading,
  HStack,
  IconButton,
  Input,
  Slider,
  SliderFilledTrack,
  SliderMark,
  SliderThumb,
  SliderTrack,
  Stack,
  Text,
} from "@chakra-ui/react";
import { useEffect, useRef, useState } from "react";
import {
  RiArrowUpSFill,
  RiDeleteBinLine,
  RiPauseFill,
  RiPlayFill,
} from "react-icons/ri";

const SPEED_OPTIONS = [0.25, 0.5, 0.75, 1, 1.25];

interface Marker {
  id: string;
  name: string;
  time: number;
}

function App() {
  const audioRef = useRef(new Audio());
  const intervalRef = useRef<NodeJS.Timeout>();
  const audio = audioRef.current;

  const progressRef = useRef<HTMLInputElement>(null);
  const fileRef = useRef<HTMLInputElement>(null);

  const [file, setFile] = useState<File>();

  const [playing, setPlaying] = useState(false);
  const [progress, setProgress] = useState(0);
  const [duration, setDuration] = useState(NaN);
  const [speed, setSpeed] = useState(1);
  const [markers, setMarkers] = useState<Marker[]>([]);
  const [markerName, setMarkerName] = useState("");

  const setTime = (time: number) => {
    audio.currentTime = time;
    setProgress(time);
  };

  const addMarker = () => {
    const name = markerName || `Marker ${markers.length + 1}`;
    const time = audio.currentTime;
    const id = `${name}-${time}-${Math.random().toString(36).slice(2, 9)}`;

    setMarkers((markers) => [...markers, { id, name, time }]);

    setMarkerName("");
  };

  useEffect(() => {
    if (playing) {
      audio.play();

      console.log(progressRef.current);
      intervalRef.current = setInterval(() => {
        setProgress(audio.currentTime);
      }, 1000);
    } else {
      audio.pause();

      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    }
  }, [audio, playing]);

  useEffect(() => {
    audio.playbackRate = speed;
  }, [audio, speed]);

  useEffect(() => {
    audio.addEventListener("loadedmetadata", () => {
      setDuration(audio.duration);
    });
  }, [audio]);

  const toTime = (time: number) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time - minutes * 60);
    return `${minutes}:${seconds < 10 ? "0" : ""}${seconds}`;
  };

  return (
    <Container maxW="container.xl" p={4} as={Stack} spacing={4}>
      {/* Upload audio file */}
      <input
        style={{ display: "none" }}
        ref={fileRef}
        type="file"
        accept="audio/*"
        onChange={(e) => {
          const file = e.target.files?.[0];
          if (file) {
            audio.src = URL.createObjectURL(file);
            setFile(file);
          }
        }}
      />

      <Button
        onClick={() => fileRef.current?.click()}
        colorScheme="green"
        alignSelf="center"
      >
        Upload
      </Button>
      <Heading size="md" textAlign="center">
        {file?.name}
      </Heading>

      <Flex direction="column" align="center" mt={4}>
        <HStack w="full" spacing={4}>
          <Text>
            {toTime(progress)} / {isNaN(duration) ? "--:--" : toTime(duration)}
          </Text>

          <Slider
            focusThumbOnChange={false}
            flex={1}
            value={progress}
            min={0}
            max={isNaN(duration) ? 1 : duration}
            onChange={(value) => {
              setProgress(value);
            }}
            onChangeEnd={(value) => {
              audio.currentTime = value;
            }}
          >
            <SliderTrack>
              <SliderFilledTrack />
            </SliderTrack>
            <SliderThumb />

            {markers.map((marker) => (
              <SliderMark
                key={marker.id}
                value={marker.time}
                onClick={() => setTime(marker.time)}
              >
                <RiArrowUpSFill />
              </SliderMark>
            ))}
          </Slider>
        </HStack>
        <IconButton
          aria-label={playing ? "Pause" : "Play"}
          rounded="full"
          colorScheme="green"
          size="lg"
          variant="outline"
          icon={playing ? <RiPauseFill /> : <RiPlayFill />}
          onClick={() => setPlaying(!playing)}
        />
      </Flex>

      <HStack alignSelf="center" spacing={2}>
        <Text>Speed</Text>

        {SPEED_OPTIONS.map((value) => (
          <Button
            key={value}
            size="sm"
            rounded="full"
            variant={speed === value ? "solid" : "outline"}
            colorScheme="green"
            onClick={() => setSpeed(value)}
          >
            {value}x
          </Button>
        ))}
      </HStack>

      <HStack>
        <Input
          placeholder="Marker name"
          type="text"
          value={markerName}
          onChange={(e) => setMarkerName(e.target.value)}
        />
        <Button colorScheme="green" onClick={addMarker}>
          Add
        </Button>
      </HStack>

      <Heading size="md" mt={4}>
        Markers
      </Heading>

      <Stack>
        {markers.length > 0 ? (
          [...markers]
            .sort((a, b) => a.time - b.time)
            .map((marker) => (
              <HStack
                key={marker.id}
                onClick={() => setTime(marker.time)}
                p={2}
                borderWidth={1}
                rounded="md"
              >
                <Text color="gray" fontSize="sm">
                  {toTime(marker.time)}
                </Text>
                <Text>{marker.name}</Text>

                <IconButton
                  icon={<RiDeleteBinLine />}
                  size="sm"
                  variant="ghost"
                  colorScheme="red"
                  aria-label={""}
                  onClick={() => {
                    setMarkers(markers.filter((m) => m.id !== marker.id));
                  }}
                />
              </HStack>
            ))
        ) : (
          <Text textAlign="center" color="gray">
            No markers set
          </Text>
        )}
      </Stack>
    </Container>
  );
}

export default App;
